import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { teachers } from 'dump/data';

const columns = [
  {
    title: 'Mã cán bộ',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Học hàm',
    dataIndex: 'academicRank',
    key: 'academicRank',
  },
  {
    title: 'Học vị',
    dataIndex: 'degree',
    key: 'degree',
  },
  {
    title: 'Chức danh',
    dataIndex: 'title',
    key: 'title',
  },
  {
    title: 'Chức vụ',
    dataIndex: 'position',
    key: 'position',
  },
  // {
  //   title: 'Not Active',
  //   dataIndex: 'notActive',
  //   key: 'notActive',
  // },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      teachers.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#teacher/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListTeacherPage extends Component {
  state = {};

  render() {
    return (
      <Card title="All techer">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/teacher/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={teachers} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTeacherPage />
  </Page>
);



